import {Box, BoxProps} from "@mui/material";
import React from "react";
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import PasswordIcon from '@mui/icons-material/Password';
import {useAppSelector} from "../../../api/redux/hooks";
import {selectLoginResponse} from "../../../api/redux/slice/SettingSlice";
import InfoAccordion from "../../../component/container/InfoAccordion";
import {DialogNames, openDialog} from "../../../component/dialog/DialogManager";

const UserCredentialPanel = ({...rest}: BoxProps) => {
  const loginResponse = useAppSelector(selectLoginResponse);
  return <Box textAlign={"start"} border={2} borderColor={"silver"} borderRadius={"6px"} {...rest}>
    <InfoAccordion infoIcon=<AccountCircleIcon/> infoTitle={"Account Name"}
                   infoValue={loginResponse?.username ?? ""}
                   onEdit={() => {
                     openDialog(DialogNames.EditUsernameDialog);
                   }}
                   infoDetail={"Account name is used for login to your account. You can edit it by clicking on it."}>
    </InfoAccordion>

    <InfoAccordion infoIcon=<PasswordIcon/> infoTitle={"Password"}
                   onEdit={() => {
                     openDialog(DialogNames.EditPasswordDialog)
                   }}
                   infoValue={"*".repeat(loginResponse === null ? 10 : Math.min((loginResponse?.username?.length ?? 0) * 2, 12))}
                   infoDetail={"Your password is encrypted. You can edit it by clicking on it. Please provide recovery email so that we can help you recover your password when you forget it."}>
    </InfoAccordion>
  </Box>;
}

export default React.memo(UserCredentialPanel);