import {Accordion, AccordionProps, AccordionSummary, Stack} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import React from "react";
import {Button, Typography} from "../wrapper/MuiWrappers";

type InfoAccordionProps = {
  infoIcon: React.ReactNode,
  infoTitle: string,
  infoValue: string | null,
  infoDetail: string,
  onEdit?: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void,
} & AccordionProps;


const InfoAccordion = ({
                         infoIcon,
                         infoTitle,
                         infoValue,
                         infoDetail,
                         children,
                         onEdit = () => ({}),
                         ...rest
                       }: InfoAccordionProps) => {


  return <Accordion disableGutters elevation={1} {...rest}>
    <AccordionSummary expandIcon={<ExpandMoreIcon/>}>
      <Stack direction={"row"} justifyContent={"center"} alignItems={"center"} flexGrow={1}
             flexWrap={"wrap"}>

        <Stack direction={"row"} spacing={1} justifyContent={"start"} sx={
          {
            width: {
              xs: "150px",
              sm: "300px",
              md: "400px",
              lg: "600px",
              xl: "500px",
            }
          }
        }>
          {infoIcon}
          <Typography variant={"body1"}>{infoTitle}</Typography>
        </Stack>

        <Typography variant={"body1"}
                    flexGrow={1}
                    textAlign={"start"}>
          <Button variant={"text"}
                  size={"small"}
                  disableElevation
                  sx={{
                    whiteSpace: "nowrap",
                    justifyContent: "flex-start",
                  }}
                  onClick={(event) => {
                    event.preventDefault();
                    onEdit(event);
                    // prevent event bubbling
                    event.stopPropagation();
                  }}>
            {(infoValue === null || infoValue === undefined || infoValue.length === 0) ? "Edit" : infoValue}
          </Button>
        </Typography>
      </Stack>

    </AccordionSummary>
    <Stack direction={"column"} justifyContent={"start"} textAlign={"start"}
           mx={3} marginBottom={2}>

      <Typography variant={"body1"}>{infoDetail}</Typography>


      {children}
    </Stack>

  </Accordion>;
}

export default InfoAccordion;